.card {
  $this: &;

  display: flex;
  flex-direction: column;

  text-align: center;

  position: relative;
  z-index: 1;
  @include tr(box-shadow .4s);

  padding: 40px 0 0;

  @include lg {
    background-color: $lighten;
    border-radius: 3px; }

  .flexslider {
    display: block;
    transition: opacity .4s, visibility .4s;

    .flex-direction-nav {
      display: none !important; }

    .flex-control-paging {
      bottom: 0;
      li {
        a {
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background-color: #ccc;
          @include tr(transform .3s);
          &.flex-active {
            background-color: #000;
            transform: scale(1.5); } } } }

    .slides {
      li {
        position: relative;
        padding-top: 230px;

        @include xs {
          padding-top: 275px; }

        picture,
        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);

          width: 100%;
          height: 100%;
          object-fit: contain; } } } }

  &:hover {
    box-shadow: 0 0 4px 0 rgba(0,0,0,.1), 0 16px 24px 0 rgba(0,0,0,.1);
    background-color: #fff;
    z-index: 2;

    #{$this}__buttons,
    #{$this}__slider {
      @include visible; }
    #{$this}__static-image {
      @include hidden; } }

  &__labels {
    position: absolute;
    z-index: 2;
    left: 50%;
    top: 15px;
    transform: translate(-50%, 0);
    width: 100%;
    text-align: center; }

  &__label {
    display: inline-block;
    background-color: $accent;
    border: 1px solid $accent;
    color: #fff;
    padding: 5px 10px;
    margin-left: 3px;
    margin-right: 3px;
    border-radius: 15px;
    text-align: center;

    &--aux {
      background-color: transparent;
      border-color: currentColor;
      color: inherit; } }

  &__slider {
    @include hidden;
    @include tr(opacity .4s, opacity .4s);
    @include lg {
      @include visible; } }

  &__inner {
    flex-grow: 1;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 25px;
    margin-bottom: 20px;
    position: relative; }

  &__title {
    font-size: 24px;
    margin-bottom: 10px; }

  &__subttl {
    color: $dark; }

  &__static-image {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 65px;
    width: 100%;
    text-align: center;

    transition: opacity .4s, visibility .4s;
    img {
      max-width: 100%; }

    @include lg {
      @include hidden; } }

  &__buttons {
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    background-color: #fff;
    box-shadow: 0 0 4px 0 rgba(0,0,0,.1), 0 16px 24px 0 rgba(0,0,0,.1);
    @include tr(opacity .4s, visibility .4s);
    @include hidden;
    @include lg {
      @include visible;
      position: static;
      box-shadow: none;
      background-color: transparent; } }

  &__button {
    display: flex;
    align-items: center;
    width: 100%;

    border: 1px solid $dark;
    padding: 14px 30px;
    color: inherit;
    @include tr(color .3s, background-color .3s);

    .icon-inline {
      margin-right: .5em;
      font-size: 1.1em; }

    &:hover {
      color: $accent; }

    &:not(:last-child) {
      border-bottom: 0; } } }
